.right-nav {
  width: 180px;
  height: 408px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate3d(180%,-50%,0);
  background: transparent url('../images//right-nav-bg.png') no-repeat right 0;
  background-size: 48px 408px;
  padding-right: 48px;
  z-index: 40;
  @include susy-breakpoint($s-320, $c8) {
      display: none;
  }
  a {
    display: block;
    margin-bottom: 42px;
    line-height: 48px;
    width: 180px;
    font-size: 30px;
    color: #b5b5b5;
    padding-right: 8px;
    box-sizing: border-box;
    position: relative;
    transition: all 300ms ease-in-out;
    &::after {
      content: "";
      width: 12px;
      height: 12px;
      border: solid 9px #303030;
      position: absolute;
      right: -39px;
      top: 50%;
      transform: translate3d(0,-50%,0) rotate(45deg);
      opacity: 0;
      transition: all 300ms ease-in-out;
    }
    div {
      width: 100%;
      text-align: justify;
      text-align-last: justify;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &:nth-child(1) {
      text-align: right;
    }
    &.active {
      color: #303030;
      &::after {
        opacity: 1;
      }
    }
  }
}

.inner {
  width: 768px;
  margin: 0 auto;
  height: 100%;
  position: relative;
}

.show-life {
  font-family: 'Noto Sans TC', sans-serif;
  background: #fff url('../images/site-bg.jpg') no-repeat center top;
}

.show-life-kv {
  height: 546px;
  background: #fff url('../images/page-kv-bg.jpg') repeat-x center top;
  &__logo {
    width: 150px;
    position: absolute;
    left: 24px;
    top: 10px;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
      height: auto;
    }
  }
  &__nav {
    position: absolute;
    left: 10px;
    top: 132px;
    a {
      color: #c1c1c1;
      font-size: 30px;
      padding-left: 2rem;
      position: relative;
      display: block;
      margin-bottom: 15px;
      &:before {
        content: "";
        background-color: #d4f5ff;
        width: 12px;
        height: 12px;
        border-radius: 50%;
        position: absolute;
        left: 7px;
        top: 50%;
        transform: translate3d(0,-50%,0);
      }
      &:after {
        content: "";
        border: solid 2px #d4f5ff;
        width: 22px;
        height: 22px;
        border-radius: 50%;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate3d(0,-50%,0);
        display: none;
      }
      &.active {
        color: #7cdfff;
        &::before {
          background-color: #7cdfff;
        }
        &::after {
          display: block;
        }
      }
    }
  }
}

.show-life-player {
  $self: &;
  height: 150px;
  background: #7cdfff url('../images/show-life-player-bg.png') repeat center top;
  background-size: 12px 17px;
  box-shadow: 1.172px 2.762px 9px 0px rgba(63, 54, 42, 0.55);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 160px;
  &__btn {
    width: 76px;
    height: 87px;
    display: inline-block;
    margin: 0 40px;
    position: relative;
    &:hover {
      #{$self}__arrow {
        background-position-y: -87px;
      }
      #{$self}__square {
        transform: translate3d(-50%,-50%,0) rotate(180deg);
        background-position-y: -111px;
      }
    }
    &.active {
      pointer-events: none;
      #{$self}__arrow {
        background-position-y: -87px;
      }
      #{$self}__square {
        transform: translate3d(-50%,-50%,0) rotate(180deg);
        background-position-y: -111px;
      }
    }
  }
  &__arrow {
    display: block;
    width: 76px;
    height: 87px;
    background: transparent url('../images/show-life-player-arrow.png') no-repeat 0 0;
    background-size: 76px auto;
    position: relative;
    z-index: 10;
  }
  &__square {
    background: transparent url('../images/show-life-player-square.png') no-repeat 0 0;
    background-size: 111px auto;
    width: 111px;
    height: 111px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%,-50%,0) rotate(0);
    z-index: 0;
    transition: transform 400ms ease-in-out;
  }
}

.tv-show {
  margin: 0 auto 66px;
  width: 763px;
  height: 530px;
  background: transparent url('../images/tv-show-bg.png') no-repeat center top;
  background-size: 763px auto;
  position: relative;
  &__slider-wrap {
    width: 720px;
    height: 406px;
    position: absolute;
    left: 50%;
    top: 90px;
    transform: translate(-50%,0,0);
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
    .swiper-button-prev {
      width: 72px;
      height: 110px;
      background: transparent url('../images/btn-prev@2x.png') no-repeat 0 0;
      background-size: 72px auto;
      top: 45%;
      &::after {
        display: none;
      }
      &:focus {
        outline: 0;
      }
    }
    .swiper-button-next {
      width: 72px;
      height: 110px;
      background: transparent url('../images/btn-next@2x.png') no-repeat 0 0;
      background-size: 72px auto;
      top: 45%;
      &::after {
        display: none;
      }
      &:focus {
        outline: 0;
      }
    }
    .custom-swiper-pagination {
      position: absolute;
      top: -100px;
      right: 30px;
    }
    .swiper-pagination-bullet {
      width: 62px;
      height: 85px;
      border-radius: 0;
      opacity: 1;
      transform: skewX(-15deg);
      margin-right: 28px;
      text-align: center;
      background-color: transparent;
      background-color: #ecd50a;
      position: relative;
      z-index: 0;
      &::after {
        width: 0; 
        height: 0; 
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        border-bottom: 10px solid #b7af20;
        position: absolute;
        content: "";
        top: 0;
        right: -4.5px;
        transform: skewX(15deg);
        z-index: -1;
      }
      &:focus {
        outline: 0;
      }
      i {
        z-index: 10;
        width: 62px;
        height: 85px;
        position: relative;
        font-family: Monitorica;
        font-size: 62px;
        color: #fff;
        line-height: 85px;
        display: inline-block;
        font-weight: 300;
        transform: translate3d(-8px,0,0) skewX(15deg);
      }
    }
    .swiper-pagination-bullet-active {
      background-color: #585858;
      &::after {
        border-bottom-color: #3c3c3c;
      }
    }
  }
}

.video-container {
  position: relative;
  padding-bottom: 56.25%;
}

.video-play,
.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video-container iframe,
.video-container object,
.video-container embed {
  pointer-events: none;
}

.page-section-title {
  background-color: #ecd50a;
  height: 67px;
  line-height: 67px;
  padding: 0 20px 0 90px;
  position: absolute;
  top: -10px;
  transform: skewX(-15deg);
  left: 30px;
  &::after {
    width: 100%;
    height: 100%;
    border: solid 2px #ecd50a;
    position: absolute;
    content: "";
    top: 4px; 
    left: 3px;
    z-index: -1;
  }
  &.center {
    top: 0;
    transform: skewX(-15deg) translate3d(-50%,0,0);
    left: 50%;
    padding-top: 8px;
    width: 550px;
    padding: 0 20px;
    text-align: center;
    line-height: 67px;
    strong {
      font-family: Monitorica;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate3d(-46%,-85%,0);
      font-size: 42px;
      color: #474747;
    }
    i {
      left: 144px;
      top: -56px;
    }
  }
  i {
    width: 52px;
    height: 80px;
    background: transparent url('../images/icon-location.png') no-repeat center top;
    background-size: 52px auto;
    display: inline-block;
    position: absolute;
    left: 18px;
    top: -16px;
    transform: skewX(15deg);
  }
  span {
    font-weight: 500;
    display: inline-block;
    font-size: 35px;
    color: #474747;
    letter-spacing: 4px;
  }
}

