.remind {
  position: relative;
  width: 768px;
  margin: 0 auto 60px;
  padding-top: 100px;
  padding-left: 40px;
  padding-right: 40px;
  box-sizing: border-box;
  &::before {
    width: 767px;
    height: 15px;
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translate3d(-50%,0,0);
    content: "";
    background: transparent url('../images/remind-bg@2x.png') no-repeat center top;
    background-size: 767px;
  }
  &__item {
    margin-bottom: 20px;
    & > p {
      font-size: 26px;
      line-height: 1.6;
      color: #8e8e8e;
      margin-bottom: 10px;
      strong {
        color: #cd0000;
      }
    }
  }
  &__wrap {
    & > p {
      font-size: 26px;
      line-height: 1.6;
      color: #8e8e8e;
      margin-bottom: 20px;
    }
    h3 {
      font-size: 26px;
      line-height: 1.6;
      color: #8e8e8e;
      margin-bottom: 8px;
    }
    ul {
      li {
        font-size: 26px;
        line-height: 1.6;
        color: #8e8e8e;
      }
    }
    ol {
      list-style: decimal;
      padding-left: 40px;
      li {
        font-size: 26px;
        line-height: 1.6;
        color: #8e8e8e;
        margin-bottom: 20px;
      }
    }
  }
}

.light-box {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1200;
  pointer-events: none;
  &.active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    background: rgba(0,0,0,0.9) url('../images/lightbox-frame.png') no-repeat center center;
    background-size: 941px 655px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn-close {
    position: absolute;
    width: 95px;
    height: 95px;
    right: -20px;
